<script>
import { SemipolarSpinner  } from 'epic-spinners'
import Navbar from "./components/Navbar";
export default {
    components: {
        Navbar,
        SemipolarSpinner 
    },
    data() {
        return {
            lang: navigator.language || navigator.userLanguage,
            isShown: false,
            isProgressing: false,
            isFalse: true
        };
    },
    created: function() {
        this.myToggler(),
        localStorage.setItem('lang', this.lang)
    },  
    methods: {
        myToggler() {
            setTimeout(function(){
                window.scrollTo(0, 0);
            }, 1);
            this.isShown = true;
            setTimeout(
                function() {
                    this.isProgressing = true;
                }.bind(this),
                500
            );
            setTimeout(
                function() {
                    this.isShown = false;
                    this.isProgressing = false;
                }.bind(this),
                3000
            );
            
        },
    },
};
