<script>
// @ is an alias to /src
import Footer from "@/components/Footer.vue";
import AnimatedNumber from "animated-number-vue";
import i18n from "../i18n";
export default {
  components: {
    AnimatedNumber,
    Footer
  },
  props: ["isShown", "isProgressing", "myToggler", "test"],
  data() {
    return {
      eyro: 1725,
      tree: 1500000,
      qtClients: 68000,
      qtKilometers: 37000000,
      num: 999999,
      value: 1000,
      scrolled: false,
      scrolled1: false
    };
  },
  mounted() {
    this.startInterval();
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    scrollTo() {
      window.scroll({
        top: 650,
        left: 0,
        behavior: "smooth"
      });
    },
    handleScroll() {
      window.scrollY > 100 ? (this.scrolled = true) : !this.scrolled;
      window.scrollY > 950 ? (this.scrolled1 = true) : !this.scrolled1;
    },
    startInterval: function() {
      setInterval(() => {
        this.tree += 1;
        this.eyro += 0.1;
        this.qtKilometers += 1;
      }, 30000);
      setInterval(() => {
        this.qtClients += 1;
      }, 60000);
    },
    formatToPrice(value) {
      return `${value
        .toFixed(0)
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, " ")}`;
    }
  },
  metaInfo: {
    titleTemplate: "Aktiv Taxi Kln",
    htmlAttrs: {
      lang: `${i18n.locale}`,
      amp: true
    }
  }
};
</script>


