<script>
import UpperBar from "./UpperBar.vue";
export default {
    name: "Navbar",
    components: {
        UpperBar
    },
    props: ["isShown", "isProgressing", "myToggler", "isFalse"],
    data() {
        return {
            z: "top",
            isActive: true,
        }
    },
    methods: {
        setLocale(locale) {
            this.$i18n.locale = locale
            this.$router.push ({
                params: {lang: locale}
            })
        },
        greet: function () {
            this.isFalse = !this.isFalse;
        },
        reload: function () {
            location.reload();
        },
    },
};
</script>